import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Base } from '../../../model/base';
import { Inspecao } from './../../../model/objetc/inspecao';
import { InspecaoService } from './../../../service/sigobra/inspecao.service';

@Component({
  selector: 'app-inspecoes',
  templateUrl: './inspecoes.page.html',
  styleUrls: ['./inspecoes.page.scss'],
})
export class InspecoesPage implements OnInit {


  inspecoes: Array<Inspecao>;
  inspecao: Inspecao;
  value: string = '';
  constructor(private base: Base, private is: InspecaoService, private router: Router) { }

  ngOnInit() {
     
  }

  ionViewWillEnter() {
    this.carregarinspecao(" ");     
  }

  excluir(inspecao: Inspecao) {
    this.base.Confirma('Deseja excluir essa inspeção', this.removerativo.bind(this), inspecao);
  }

  remover(inspecao: Inspecao) {
    this.base.present();   
    return this.is.delete(inspecao).subscribe(
      data => {
        this.base.dismiss();
        this.base.menssagemSucesso('Inspeção Excluida');
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  inspeção '+ inspecao.id + this.base.tratarErro(error));  
      }
    );
  }



  editar(inspecao: Inspecao) { 
    this.is.inspecao = inspecao;
    this.router.navigate(['inspecao-inserir']);   
  }
  
  
  carregarinspecoes(value: string) {
    this.base.present();
    return this.is.pesquisar(value).subscribe(
      data => {
        this.base.dismiss();
        this.inspecoes = data as Array<Inspecao>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  inspecao ' + this.base.tratarErro(error));
      }
    );
  }

  carregarinspecao(value: string) {
    this.base.present();
    return this.is.pesquisa(value).subscribe(
      data => {
        this.inspecoes = data as Array<Inspecao>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  inspecao ' + this.base.tratarErro(error));
      }
    );
  }

  inserir() {
    this.is.inspecao = new Inspecao;
    this.router.navigate(['inspecao-inserir']);
  }

  removerativo(inspecao: Inspecao) {
    this.inspecao = inspecao;
    this.base.present();  
    if ( (this.inspecao.ativo !== null)&& (this.inspecao.ativo !== undefined)){      
      this.inspecao.ativo = false;
    }  
    return this.is.editarativo(this.inspecao).subscribe(
      data => {        
        this.base.dismiss();
        this.router.navigate(['inspecoes']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  inspecao '+ this.inspecao.id + this.inspecao.ativo 
        + this.base.tratarErro(error));  
      }
    );           
  }

  realizar(inspecao: Inspecao) { 
    this.is.inspecao = inspecao;
    this.router.navigate(['inspecao-realizar']);   
  }

  getRelatorioInspecao(inspecao: Inspecao) {
    return this.is.getRelatorioInspecao(inspecao).subscribe(
      data => {
        this.base.abrirPDF(data, 'declaração');
      }, error => {
        this.base.dismiss();
        console.log(error);
        this.base.mensagemErro('Gerar Declaração' + inspecao.local 
          + this.base.tratarErro(error));
      }
    );
  }

}


